import * as React from 'react';
import Card from '@mui/material/Card';
// import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import { Divider, Grid } from '@mui/material';


export default function StockCard({ price, stockQtt, spentMoney, averagePrice, evalMoney }) {

    const result = evalMoney - spentMoney
    const roi = result / spentMoney * 100 || 0
    return (
        <Card sx={{ minWidth: 275 }}>
            <CardContent>
                <Grid container spacing={2}>
                    <Grid item xs={6}>

                        <Typography color={"text.secondary"}></Typography>
                        <Typography color={"text.primary"}>{price.toLocaleString()}</Typography>
                    </Grid>
                    <Grid item xs={6}>
                        <div> {result.toLocaleString()}</div>
                        <div> {roi}%</div>
                    </Grid>
                    <Divider />
                    <Grid item xs={6}>

                        <Typography color={"text.secondary"}></Typography>
                        <Typography color={"text.primary"}>{stockQtt}</Typography>
                    </Grid>
                    <Grid item xs={6}>

                        <Typography color={"text.secondary"}></Typography>
                        {/* <Typography color={"text.primary"}>{averagePrice.toLocaleString() || 0}</Typography> */}
                        <Typography color={"text.primary"}>{Number.isNaN(averagePrice) ? 0 : averagePrice.toLocaleString()}</Typography>
                    </Grid>
                    <Grid item xs={6}>
                        <Typography color={"text.secondary"}></Typography>
                        <Typography color={"text.primary"}>{evalMoney.toLocaleString()}</Typography>
                    </Grid>
                    <Grid item xs={6}>

                        <Typography color={"text.secondary"}></Typography>
                        <Typography color={"text.primary"}>{spentMoney.toLocaleString()}</Typography>
                    </Grid>
                </Grid>
            </CardContent>
            {/* <CardActions>
                <Button size="small">Learn More</Button>
            </CardActions> */}
        </Card>
    );
}